.tabs {
  position: fixed;
  align-items: center;
  width: 100vw;
  top:20px;
  z-index: 10000000000;
}
.tabin {
  position: relative;
  margin:0 auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.section>div:last-child {
  padding-right: 50px;
}
.section::-webkit-scrollbar {
  width: 5px;
  height: 8px;
}
